.App-lightning {  
  height: 40vmin;
  width: 100%;
  margin: auto;
  position: absolute;
  z-index:1;
  display: flex;
  flex-direction: column;
}

.App-logo {
  /* animation: App-logo-spin infinite 20s linear; */
  height: 40vmin;
  
  z-index:3;
}
.App-logo-hide {
  opacity: 0.05;
  /* animation: App-logo-spin infinite 20s linear; */
  height: 40vmin;
  width: 100vmax;
  z-index:1;
}

.App-scene {
  z-index: 0;
  height: 40vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}
.App-header {
  /* background-color: #000f14;
  height: 40vh;
  display: flex; */
  
  
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;

  max-height: 229px;
  display: block;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.footer-container{
  margin:20px;
